html{
    font-size: 10px;
}


html,
body {
    padding: 0;
    margin: 0;
}

html,
body,
#root,
.App ,
.fullHeight { 
    height: 97.8%;
}



*, ::before, ::after {
   -webkit-box-sizing: border-box;
   -moz-box-sizing: border-box;
   box-sizing: border-box;
}

body {
    font-family: 'Open Sans', sans-serif;
    font-size: 1.8rem;
    font-weight: 400;
    line-height: 1;
}

h1 {
    display: block;
    width: 100%;
    padding: 0;
    margin: 0 auto 2rem;
    font-size: 3.0rem;
    line-height: 3.2rem;
    color: black;
    text-align: left;
    font-weight: 400;
    text-transform: uppercase;
    border-bottom: #d3d3d3;
}

h2 {
    display: block;
    width: 100%;
    padding: 0;
    margin: 0 auto 2rem;
    font-size: 2.5rem;
    line-height: 2.7rem;
    color: black;
    text-align: left;
    font-weight: 400;
    text-transform: uppercase;
    border-bottom: #d3d3d3;
}

a {
    text-decoration: none;
    cursor: pointer;
}

a, a:hover{
    -webkit-transition: all .4s ease-in-out;
    -moz-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out;
}

.main {
    width: 100% ;
    height: calc(100% - 8.5rem);
    max-width: 1590px;
    padding : 0 10px;
    margin: 0 auto;
}

.controlPanel {
    position: relative;
    display: inline-block;
    width: 100%;
    min-height: 100%;
    padding: 3rem 0 5rem 25.0rem;
    border-bottom: 1px solid #d3d3d3;

    .sidebar {
        position: absolute;
        top: 0; left: 0;
        width: 25.0rem;
        height: 100%;
        z-index: 1;
        border-right: 1px solid #d3d3d3;
    }


    .content {
        padding: 0 10px;

    }
}


.adminLayout {
    height: calc(113% - 6.5rem - 5.8rem - 3.6rem);
}

.dashboardLayout {
    height: calc(113% - 6.5rem - 5.8rem );
}